@use "base/variables" as v;
@use "base/mixins" as m;

.eventos-registro {
  @include m.contenedor;
  @include m.tablet {
    display: flex;
    align-items: flex-start;
    gap: 2rem;
  }

  &__listado {
    @include m.tablet {
      flex: 0 0 60%;
    }
  }
  &__grid {
    @include m.grid(1, 3rem);
    @include m.tablet {
      @include m.grid(2, 3rem);
    }
  }
  &__heading {
    &--conferencias,
    &--workshops {
      white-space: nowrap;
      @include m.texto-fluido(2rem, 5rem);
    }
    &--conferencias {
      color: v.$azul;
    }
    &--workshops {
      color: v.$secundario;
      margin-top: 5rem;
    }
  }
  &__fecha {
    margin: 6rem 0 3rem 0;
  }
}

.registro {
  padding: 3rem;
  background-color: v.$gris-claro;
  border-radius: 1rem;
  @include m.tablet {
    flex: 0 0 40%;
    position: sticky;
    position: -webkit-sticky;
    top: 3rem;
  }
  &__heading {
    margin: 0 0 3rem 0;
  }
  &__evento {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    &:nth-child(even){
      background-color: darken(v.$gris-claro, 3%);
    }
  }
  &__nombre {
    font-size: 2rem;
    font-weight: v.$bold;
    margin:0;
  }
  &__eliminar {
    all:unset;
    color: v.$rojo;

    &:hover{
      cursor: pointer;
      
    }
  }
  &__regalo{
    margin-top: 3rem;
  }
  &__label{
    display: block;
    font-size: 2rem;
    font-weight: v.$bold;
    margin-bottom: 1rem;
  }
  &__select{
    width: 100%;
    background-color: v.$blanco;
    padding: 1rem;
    border: none;
    border-radius: 1rem;
    cursor: pointer;
    outline: none;
  }
}
